.btn {
	padding: 11px 20px;
	font-family: $proxima;
	color: $greyHigh;
	@include f(18px,400,1);
	vertical-align: top;
	display: inline-block;
	border-radius: 20px;
	background-color: rgba(152, 149, 162, 0.1);
	transition: background-color .3s, color .3s;
	@include hover {
		color: $dark;
		background-color: rgba(152, 149, 162, 0.2); }

	&_border-read {
		color: $red;
		box-shadow: inset 0 0 0 1px $red;
		background-color: $white;
		@include hover {
			color: $white;
			background-color: $red; } }

	&_md {
		padding: 11px 32px; } }

.filter-btn {
	background-color: transparent;
	font-family: $proxima;
	font-size: 18px;
	line-height: 1.667;
	color: $greyMid;
	&.is-active {
		color: $red; } }

.anim-btn {
	position: relative;
	display: inline-block;
	padding-right: 48px;
	font-family: $proxima;
	line-height: 1.667;
	font-size: 18px;
	color: $greyHigh;
	text-decoration: none;
	background-color: transparent;
	&:hover {
		.anim-btn__text {
			transform: translateX(24px); }
		&:after {
			transform: translate(0px, -50%); } }
	&__text {
		display: inline-block;
		vertical-align: top;
		transition: transform 0.8s; }
	&:after {
		content: '';
		position: absolute;
		top: 50%;
		left: 100%;
		transform: translate(-32px, -50%);
		@include arr(6, 10, $red, r);
		transition: transform 0.8s; } }
