.clients {
  height: 100vh;
  @include vertical;
  &__inner {
    display: inline-block;
    vertical-align: middle; }
  &__title {
    margin-bottom: 30px; }
  &__descr {
    line-height: 1.4;
    font-family: $proxima;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.667;
    color: $greyHigh; }
  &__logos {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 -45px; }
  &__logo {
    padding: 0 45px; } }
