//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

$proxima: 'ProximaNova';
$sofia: 'SofiaPro';
$montserrat: 'Montserrat';

@font-face {
  @include fontdefault(normal, 400, $proxima);
  @include fontpath('ProximaNova-Regular', 'ProximaNova-Regular', ProximaNova-Regular); }

@font-face {
  @include fontdefault(normal, 600, $proxima);
  @include fontpath('ProximaNova-Semibold', 'ProximaNova-Semibold', ProximaNova-Semibold); }

@font-face {
  @include fontdefault(normal, 700, $proxima);
  @include fontpath('ProximaNova-Bold', 'ProximaNova-Bold', ProximaNova-Bold); }

@font-face {
  @include fontdefault(normal, 300, $sofia);
  @include fontpath('SofiaProLight', 'SofiaProLight', SofiaProLight); }

@font-face {
  @include fontdefault(normal, 400, $sofia);
  @include fontpath('SofiaProRegular', 'SofiaProRegular', SofiaProRegular); }

@font-face {
  @include fontdefault(normal, 900, $sofia);
  @include fontpath('SofiaProBlack', 'SofiaProBlack', SofiaProBlack); }

