.main-loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: #fff;
  transition: opacity 1s, visibility 1s;
  text-align: center;
  font-size: 0;
}

.main-loader:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.main-loader.is-hidden {
  opacity: 0;
  visibility: hidden;
}

.main-loader__text {
  position: relative;
  line-height: 1;
  font-size: 200px;
  color: #fff;
  font-family: "SofiaPro", Helvetica Neue, Helvetica, Arial, sans-serif;
  vertical-align: middle;
  font-weight: 900;
  text-shadow: 0px 20px 69px rgba(78, 73, 122, 0.11);
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
  display: inline-block;
}

body.is-loaded .main-loader__text {
  opacity: 1;
  visibility: visible;
  transition-delay: 1s;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 61px;
}

.header__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.header__menu {
  display: none;
}

.logo img {
  max-width: 100%;
  display: block;
}

.hero {
  padding: 120px 0 80px;
  min-height: 100vh;
  display: flex;
  align-items: center;
}

.works {
  background-color: rgba(255, 111, 97, 0.2);
  padding: 227px 0 227px;
  overflow: hidden;
}

.works__title-wrapper {
  margin-bottom: 80px;
}

.works__title {
  position: relative;
  margin-bottom: 30px;
}

.works__title_filter span {
  display: inline-block !important;
}

.works__title_filter:after {
  content: '';
  display: inline-block;
  vertical-align: middle;
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 0 5px 6px 5px;
  border-color: transparent transparent #F03454 transparent;
  transition: opacity 0.8s 1s, transform 0.8s;
  opacity: 0;
}

.is-animate .works__title_filter:after {
  opacity: 1;
}

.is-open .works__title_filter:after {
  transform: rotate(-180deg);
}

.works__container {
  position: relative;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;
}

.works__container.is-init {
  opacity: 1;
  visibility: visible;
}

.works__item {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1023px) {
  .works__item {
    width: calc(50% - 50px);
  }
}

.works__item_padding {
  position: absolute;
  right: 0 !important;
  top: 0 !important;
  left: auto !important;
  float: right;
  height: 200px;
}

.works__filter-wrapper {
  margin-bottom: 82px;
}

.works__filter {
  display: none;
  margin-top: 87px;
}

.works__filter-btn {
  background-color: transparent;
}

.works__btn {
  width: 100%;
  position: absolute;
  width: 100%;
  left: 0 !important;
  height: 30px;
}

.works__btn:nth-child(even) {
  top: auto !important;
  bottom: 140px !important;
}

.works__btn .anim-line {
  margin-right: 40px;
}

.work {
  padding-bottom: 110px;
}

.work__image-wrap {
  overflow: hidden;
  transition: transform 0.3s;
  background-color: #ccc;
  backface-visibility: hidden;
}

.no-touch .work__image-wrap:hover {
  transform: scale(0.97);
}

.no-touch .work__image-wrap:hover .work__image_scale {
  transform: scale(1.1);
}

.work__image {
  transition: transform 0.3s;
}

.work__image img {
  max-width: 100%;
  display: block;
}

.js-masonry-width {
  width: calc(50% - 60px);
}

@media only screen and (max-width: 1023px) {
  .js-masonry-width {
    width: calc(50% - 50px);
  }
}

.js-masonry-gutter {
  width: 120px;
}

@media only screen and (max-width: 1023px) {
  .js-masonry-gutter {
    width: 100px;
  }
}

.filter-controls {
  display: flex;
  align-items: center;
}

.filter-controls__item {
  flex: 0 0 20%;
  min-width: 20%;
}

.anim-line {
  display: inline-block;
  vertical-align: middle;
  height: 1px;
  width: 40px;
  background-color: #9F9CA9;
}

[data-anim] {
  position: relative;
}

[data-anim].is-animate {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

[data-anim-text-parent] {
  overflow: hidden;
  position: relative;
  display: block;
}

[data-anim="text-from-bottom"] {
  display: inline-block;
  position: relative;
  transform: translateY(102%);
}

[data-rows] [data-anim="text-from-bottom"] {
  transition: inherit;
}

[data-anim-inner="line-from-left"] {
  transform: translateX(-100%);
  opacity: 0;
}

[data-anim="from-bottom"] {
  transition: opacity 1s, visibility 1s, transform 1s;
  transform: translateY(20%);
  opacity: 0;
}

[data-rows] {
  visibility: hidden;
}

[data-rows].is-animate,
[data-scroll-trigger].is-animate [data-rows] {
  visibility: visible;
}

[data-anim="from-top"] {
  position: relative;
  transform: translateY(-35px);
  opacity: 0;
}

[data-anim="sphere-from-top"] {
  position: relative;
  transform: translateY(-350px);
}

[data-anim="from-left"] {
  position: absolute;
  transform: translateX(-100%);
}

