[data-tabs] [data-tabs-control] {
  cursor: pointer;
  user-select: none;
}

[data-tabs] [data-tabs-control][disabled], [data-tabs] [data-tabs-control].is-disabled {
  cursor: not-allowed;
  user-select: none;
}

[data-tabs] [data-tabs-wrapper] {
  position: relative;
}

[data-tabs] [data-tabs-container] {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
}

[data-tabs] [data-tabs-container].is-open {
  position: relative;
  opacity: 1;
  visibility: visible;
}

.tabs {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.tabs__out {
  width: 50%;
}

.tabs__wrapper {
  width: 50%;
}

.tabs__container {
  padding: 4px 0 0 140px;
}

.tabs__list {
  counter-reset: number;
}

.tabs__subitem {
  position: relative;
  padding-left: 31px;
}

.tabs__subitem:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #F03454;
}

.tabs__subitem:not(:first-child) {
  margin-top: 20px;
}

.menu-item {
  position: relative;
  padding: 0 40px 0 60px;
  font-family: "SofiaPro", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 28px;
  color: #9F9CA9;
  transition: color .3s;
  display: flex;
  align-items: center;
}

.no-touch .menu-item:hover {
  color: #817D8E;
}

.menu-item:before {
  counter-increment: number;
  content: "0" counter(number);
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 14px;
}

.menu-item__line {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  transform: translateX(-100%);
  right: 0;
  width: 40px;
  height: 1px;
  background-color: #F03454;
}

.menu-item:not(:first-child) {
  margin-top: 10px;
}

.menu-item.is-active {
  color: #F03454;
}

.no-touch .menu-item.is-active:hover {
  color: #F03454;
}

.menu-item.is-active .menu-item__line {
  visibility: visible;
}

