.btn {
  padding: 11px 20px;
  font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif;
  color: #817D8E;
  font-size: 18px;
  font-weight: 400;
  line-height: 1;
  vertical-align: top;
  display: inline-block;
  border-radius: 20px;
  background-color: rgba(152, 149, 162, 0.1);
  transition: background-color .3s, color .3s;
}

.no-touch .btn:hover {
  color: #3E3852;
  background-color: rgba(152, 149, 162, 0.2);
}

.btn_border-read {
  color: #F03454;
  box-shadow: inset 0 0 0 1px #F03454;
  background-color: #fff;
}

.no-touch .btn_border-read:hover {
  color: #fff;
  background-color: #F03454;
}

.btn_md {
  padding: 11px 32px;
}

.filter-btn {
  background-color: transparent;
  font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 18px;
  line-height: 1.667;
  color: #9F9CA9;
}

.filter-btn.is-active {
  color: #F03454;
}

.anim-btn {
  position: relative;
  display: inline-block;
  padding-right: 48px;
  font-family: "ProximaNova", Helvetica Neue, Helvetica, Arial, sans-serif;
  line-height: 1.667;
  font-size: 18px;
  color: #817D8E;
  text-decoration: none;
  background-color: transparent;
}

.anim-btn:hover .anim-btn__text {
  transform: translateX(24px);
}

.anim-btn:hover:after {
  transform: translate(0px, -50%);
}

.anim-btn__text {
  display: inline-block;
  vertical-align: top;
  transition: transform 0.8s;
}

.anim-btn:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-32px, -50%);
  width: 0px;
  height: 0px;
  border-style: solid;
  border-width: 5px 0 5px 6px;
  border-color: transparent transparent transparent #F03454;
  transition: transform 0.8s;
}
